@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro);
@charset "utf-8";

*, body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Ubuntu', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#skip-nav {
  position: absolute;
  left: 0.5rem;
  background-color: rgba(255, 127, 80, 0.9);
  padding: 0.5rem 1.5rem;
  border-radius: 0 0 0.25rem 0.25rem;
  z-index: 9999;
  -webkit-transform: translateY(-120%);
          transform: translateY(-120%);
  transition: -webkit-transform 325ms ease-in;
  transition: transform 325ms ease-in;
  transition: transform 325ms ease-in, -webkit-transform 325ms ease-in;
}

#skip-nav:focus {
  -webkit-transform: translateY(0);
          transform: translateY(0);
}

hr {
  border: none;
  border-radius: 25px;
  height: 10px;
  width: 100%;
  background:  
    linear-gradient(135deg, var(--elem-background) 35%, transparent 25%) -5px 0,
    linear-gradient(225deg, var(--elem-background) 35%, transparent 25%) -5px 0,
    linear-gradient(315deg, var(--elem-background) 35%, transparent 25%),
    linear-gradient(45deg, var(--elem-background) 35%, transparent 25%);	
  background-size: 10px 10px;
  background-color: var(--text-title);
  transition: background-size 0.25s ease-in;
}

p {
  color: var(--text-primary);
}

strong {
  background-image: linear-gradient(120deg, var(--text-title) 0%, var(--text-interactive) 100%);
  background-repeat: no-repeat;
  background-size: 100% 0.2em;
  background-position: 0 100%;
  transition: background-size 0.25s ease-in;
}

strong:hover {
  background-size: 100% 90%;
  cursor: pointer;
}

.success-msg {
  color: #4F8A10;
  /* background-color: #DFF2BF; */
}

.warning-msg {
  color: #9F6000;
  /* background-color: #FEEFB3; */
}

.error-msg {
  color: #D8000C;
  /* background-color: #FFD2D2; */
}

/* SMALL: MOBILE */
@media only screen and (max-width: 640px) {
  p {
    font-size: 12px;
  }
  section {
    padding: 0 10%;
  }
}

/* MEDIUM: TABLET */
@media only screen and (min-width: 1007px) {
  p {
    font-size: 14px;
  }
  section {
    padding: 0 15%;
  }
}

/* LARGE: DESKTOP */
@media only screen and (min-width: 1007px) {
  p {
    font-size: 15px;
  }
}

/* Animation */

.polka-dot {
  background: transparent;
  background-image:
        radial-gradient(var(--text-title) 20%, transparent 0), 
        radial-gradient(var(--text-title) 20%, transparent 0);
  background-size: 30px 30px;
  background-position: 0 0, 15px 15px;
}

@-webkit-keyframes MoveUpDown {
  0%, 100% {
      bottom: 5px;
  }
  50% {
      bottom: 10px;
  }
}

@keyframes MoveUpDown {
  0%, 100% {
      bottom: 5px;
  }
  50% {
      bottom: 10px;
  }
}

@-webkit-keyframes barber-pole-illusion {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}

@keyframes barber-pole-illusion {
  0% {
    background-position: 0% 0%;
  }
  100% {
    background-position: 100% 100%;
  }
}

