/* [data-theme='light'] */
:root {
  --background: #ebeaef;
  --primary-background: #f2f2f4;
  --secondary-background: #fafafc;
  /* --primary-background-disabled: none; */
  /* --secondary-background-disabled: none; */

  --text-primary: #1b242e;
  --text-secondary: #252933;

  --text-title: #007acb; /* #66fcf1 #007acb #868dc3; */
  --text-interactive: #66fcf1; /* rgba(255, 127, 80, 0.9) */

  --size-6: 3rem;
  --size-5: 2rem;
  --size-4: 1.75rem;
  --size-3: 1.5rem;
  --size-2: 1.25rem;
  --size-1: 1.15rem;
  
  --text-disabled: #c4c4c4;
  --text-error: #be475d;
  --text-success: #4ab66f;
}

[data-theme='dark'] {
  --background: #1e1e1e; /* #0b0710 */
  --primary-background: rgba(30, 30, 30, 0.75); /* #252526 #191919 */
  --secondary-background: rgba(37, 37, 38, 0.75); /* #2d2d30 #808080 */

  --text-title: #be3144;
  --text-interactive: #fcb045;
  
  --text-primary: #dddddd;
  --text-secondary: #fcfcff;
  
  
  
  --glass-background: #0b071011; /* #0b071080 */
}

[data-palette='sunset'] {
  --text-title: #be3144;
  --text-interactive: #fcb045;
}

[data-palette='purple'] {
  --text-title: #9896f1;
  --text-interactive: #d59bf6;
}

[data-palette='moonlight'] {
  --text-title: #007acb;
  --text-interactive: #66fcf1;
}

[data-palette='green'] {
  --text-title: #36d1c4;
  --text-interactive: #7dd87d;
}

[data-palette='grey'] {
  --text-title: #393e46;
  --text-interactive: #d9dad7; /* #222831 */
}
::-webkit-scrollbar {
    width: 0.7em;
    height: 1em;
}

::-webkit-scrollbar-thumb {
    background-color: var(--text-title); /* hsl(352, 59%, 47%) */
    opacity: 0.8;
    /* filter: alpha(opacity=80); */
    border: .125em solid var(--background); /* hsl(0, 0%, 13%) */
    border-radius: 100vw;
}

::-webkit-scrollbar-thumb:hover {
    background-color: var(--text-title); /* hsl(352, 59%, 47%) */
    opacity: 1;
    filter: alpha(opacity=100);
}

::-webkit-scrollbar-track {
    background-color: var(--background); /*hsl(0, 0%, 87%);*/
    opacity: 0.8;
    /* filter: alpha(opacity=80); */
    border-radius: 100vw;
    margin-block: .5em;
}

::-webkit-scrollbar-track:hover {
    background-color: var(--secondary-background);
    opacity: 1;
}

@supports (scrollbar-color: red blue) {
    * {
        scrollbar-width: normal;
        scrollbar-color: var(--text-title) var(--secondary-background); /* hsl(352, 59%, 47%) hsl(0, 0%, 13%) */
    }
}
html {
  scroll-behavior: smooth;
  /* overflow-x: hidden; */
  background-color:var(--background);
}

#layout-wrapper {
  max-height: 100vh;
  max-width: 100vw;
  display: grid;
  grid-template-areas:
    "header"
    "content"
    "footer";
  grid-template-columns: 1fr;
  grid-template-rows: auto 1fr auto;
  grid-gap: 20px;
  background-color: var(--background);
  color: var(--text-primary);
  /* overflow-x: hidden; */
}

#header-wrapper {
  grid-area: header;
  /* width: 100vw; */
  /* height: 100vh; */
  /* margin-top: 2rem; */
  z-index: 9999;
}

#footer-wrapper {
  grid-area: footer;
  /* margin-bottom: 2rem; */
}

#content-wrapper {
  margin-top: 1.25rem;
  margin-bottom: 2rem;
  /* max-height: 100vh;
  max-width: 100vw; */
  grid-area: content;
  overflow-y: scroll;
  display: flex;
  flex-direction: column;
  align-items: center;
}

#content-wrapper {
  background: transparent;
  z-index: 1;
}

#content-wrapper section > div:not(.section-title) {
  z-index: 1000;
  background-color: var(--primary-background); /* --elem-glass */
  border: 12px solid rgba(var(--text-title), 0.5);
  mix-blend-mode: luminosity;
}

section {
  /* position: relative; */
  margin: 0.5rem 0;
  width: 100%;
  max-width:100vw;
  height: auto;
  /* min-height:100vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
}

.flex-vertical {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
}

.flex-horizontal {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
}

.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.hidden {
  display: none;
}
button,
.styled-input {
    z-index: 1000;
}

.styled-input {
    width: 100%;
    margin: 0.8rem 0;
    position: relative;
    border-radius: 4px;
}

.styled-input label {
    color: var(--text-secondary);
    position: absolute;
    top: 0;
    left: 0;
    /* padding: 1.3rem 30px 1rem 30px; */
    padding: 1rem 1rem;
    transition: all 0.25s ease;
    pointer-events: none;
}

input,
textarea,
select {
    padding: 1rem;
    border: 2px solid var(--text-title);
    width: 100%;
    /* font-size: 1rem; */
    background-color: var(--elem-background-alpha);
    color: var(--text-secondary);
    border-radius: 4px;
}

textarea {
    min-width: 100%;
    max-width: 10em;
    min-height: 10em;
    /* height: 100%; */
    max-height: 25em;
}

select {
    border-radius: 15px; /* 50px; */
    -webkit-appearance: none;
    appearance: none;
    background-image:
      linear-gradient(45deg, transparent 50%, var(--text-primary) 50%),
      linear-gradient(135deg, var(--text-primary) 50%, transparent 50%),
      linear-gradient(to right, var(--text-title), var(--text-title));
    background-position:
      calc(100% - 20px) calc(1.25rem + 2px),
      calc(100% - 15px) calc(1.25rem + 2px),
      calc(100% - 2.75em) 0;
    background-size:
      5px 5px,
      5px 5px,
      1.75px 100%;
    background-repeat: no-repeat;
    cursor: pointer;
}

select:focus {
    background-image:
      linear-gradient(45deg, var(--text-primary) 50%, transparent 50%),
      linear-gradient(135deg, transparent 50%, var(--text-primary) 50%),
      linear-gradient(to right, var(--text-interactive), var(--text-interactive));
    background-position:
        calc(100% - 15px) calc(1.25rem + 2px),
        calc(100% - 20px) calc(1.25rem + 2px),
        calc(100% - 2.75em) 0;
    background-size:
      5px 5px,
      5px 5px,
      1.75px 100%;
    background-repeat: no-repeat;
    border-color: var(--text-interactive);
    outline: 0;
}

input:focus,
textarea:focus,
select:focus { 
    outline: 0;
    border: 2px solid var(--text-interactive);
}

input:focus ~ span,
textarea:focus ~ span {
    width: 100%;
    transition: all 0.075s ease;
}

input:focus ~ label, textarea:focus ~ label, input:valid ~ label, textarea:valid ~ label {
    font-size: 0.7rem;
    color: var(--text-secondary);
    top: -13px;
    transition: all 0.225s ease;
}

button {
  width: 100%;
  max-width: 20rem;
  font-size: 1rem;
  margin: 3rem 0;
  padding: 1rem;
  border-radius: 50px!important;
  border: 2px solid var(--text-title);
  /* box-shadow: 0 0 5px var(--text-title); */
  color: var(--text-secondary);
  background-color: var(--elem-background-alpha);
  cursor: pointer;
  -webkit-animation: MoveUpDown 0.7s linear infinite;
          animation: MoveUpDown 0.7s linear infinite;
}

button:hover {
    opacity: 1;
    border: 2px solid var(--text-interactive);
    -webkit-transform: translateY(1px);
            transform: translateY(1px);
    box-shadow: 0 1px 1px 0 rgba(0,0,0,0.10),
              0 1px 1px 0 rgba(0,0,0,0.09);
}

button a {
  color: var(--text-primary);
  text-decoration: none;
}
.animated-border {
    border: 2px solid transparent;
    background-image: transparent;
}
.animated-border::before, .animated-border::after {
    position: absolute;
    content: "";
    width: 20px;
    height: 20px;
}
.animated-border::before {
    top: -2px;
    left: -2px;
    border-top: 2px solid var(--text-title);
    border-left: 2px solid var(--text-title);
    transition: 0.5s all;
}
.animated-border::after {
    bottom: -2px;
    right: -2px;
    border-bottom: 2px solid var(--text-title);
    border-right: 2px solid var(--text-title);
    transition: 0.5s all;
}
.animated-border:hover::before, .animated-border:hover::after {
    width: 100.2%;
    height: 100%;
}
#bubble-background {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: transparent;
    z-index: 0;
}

#bubble-background span {
    position: absolute;
    border-radius: 50%;
    background-image: linear-gradient(45deg, var(--text-title), var(--text-interactive));
    border-radius: 38% 62% 55% 45% / 32% 53% 47% 68% ;
    -webkit-animation: morph-bubble 7s linear infinite;
            animation: morph-bubble 7s linear infinite;
}

#bubble-background > span:nth-child(1) {
    top: 10vh;
    right: 10vw;
    width: 75px;
    height: 75px;
}
#bubble-background > span:nth-child(2) {
    top: 3vh;
    left: 10vw;
    width: 50px;
    height: 50px;
    border-radius: 38% 62% 33% 67% / 60% 53% 47% 40%;
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}
#bubble-background > span:nth-child(3) {
    right: 15vw;
    bottom: 5vh;
    width: 20px;
    height: 20px;
    border-radius: 38% 62% 55% 45% / 52% 53% 47% 48%;
}
#bubble-background > span:nth-child(4) {
    bottom: 25vh;
    left: 5vw;
    width: 80px;
    height: 80px;
    border-radius: 38% 62% 55% 45% / 52% 53% 47% 48%;
}
#bubble-background > span:nth-child(5) {
    right: 7vw;
    bottom: 10vh;
    width: 25px;
    height: 25px;
    border-radius: 38% 62% 55% 45% / 52% 53% 47% 48%;
}
#bubble-background > span:nth-child(6) {
    top: 5vh;
    right: 20vw;
    width: 25px;
    height: 25px;
}
  
@-webkit-keyframes morph-bubble {
    20% {
        border-radius: 45% 55% 62% 38% / 53% 51% 49% 47%;
    }
    40% {
        border-radius: 45% 55% 49% 51% / 36% 51% 49% 64%;
    }
    60% {
        border-radius: 60% 40% 57% 43% / 47% 62% 38% 53%;
    }
    80% {
        border-radius: 60% 40% 32% 68% / 38% 36% 64% 62%;
    }
}
  
@keyframes morph-bubble {
    20% {
        border-radius: 45% 55% 62% 38% / 53% 51% 49% 47%;
    }
    40% {
        border-radius: 45% 55% 49% 51% / 36% 51% 49% 64%;
    }
    60% {
        border-radius: 60% 40% 57% 43% / 47% 62% 38% 53%;
    }
    80% {
        border-radius: 60% 40% 32% 68% / 38% 36% 64% 62%;
    }
}
.nav-items > li,
.social-items > li,
.scroll-items > li,
.setting-items > li {
    margin: 0.5rem;
    list-style: none;
}

.setting-items-container > li {
    margin: 0.5rem;
