    background-color: var(--text-title);
    border-radius: 25px;
    padding: 0 0.5rem;
    padding-bottom: 0.2rem;
}

.project-tech-stack span a {
    color: var(--text-primary);
    text-decoration: none;
    font-size: 0.9em;
    font-weight: 400;
    letter-spacing: 1px;
}

.project-tech-stack span:hover {
    opacity: 1;
}

@media only screen and (max-width: 640px) {
    .project-container {
        flex-direction: column;
    }
}

.project-image {
    position: relative;
    flex-shrink: 0;
    flex-basis: 49%;
    /* min-width: 50vw; */
    height: 100%;
    z-index: 1000;
    cursor: pointer;
}

.image-wrapper {
    width: 100%;
    height: 100%;
    /* padding-bottom: 40px; */
}

.image-border {
    position: absolute;
    top: 10px;
    left:10px;
    border: 2px solid var(--text-title);
    border-radius: 1px;
    z-index: 1;
    transition: 0.2s ease-in;
}

.image-cover {
    position: absolute;
    background-color: var(--text-title);
    transition: 0.3s ease-in;
    border-radius: 1px;
    z-index: 2000;
    opacity: 0;
}

.image-wrapper img {
    position: relative;
    z-index: 2;
    border-radius: 1px;
    margin-right: 30px;
}

@media(hover:hover){
    .image-wrapper:hover .image-cover{
        opacity: 0.4;
    }
    .image-wrapper:hover .image-border{
        top: 6px;
        left: 6px;
    }
}
